html, body {
  max-width: 400px;
  min-height: 400px; }

body {
  font-family: "Roboto", sans-serif; }

.card {
  display: none; }
  .card.visible {
    display: block; }
  .card#online {
    min-height: 106px; }

button {
  display: block;
  width: 100%;
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  background: none;
  font-family: "Roboto";
  cursor: pointer; }
  button h2 {
    display: flex;
    align-items: center;
    width: 100%;
    margin: 0;
    padding: 8px;
    font-weight: 400; }
    button h2 i.material-icons {
      font-size: 1.3em; }

p {
  margin-left: 8px; }

.clearfix::after {
  display: table;
  clear: both;
  content: ''; }

.container {
  width: 100%;
  max-width: 1180px;
  margin-right: auto;
  margin-left: auto;
  padding-right: 20px;
  padding-left: 20px; }

.hide-text {
  padding: 0;
  text-indent: 101%;
  white-space: nowrap;
  overflow: hidden; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  overflow: hidden;
  clip: rect(0 0 0 0); }

.blink-blink {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.2);
  animation: blink-in .1s ease-in-out forwards; }
  .blink-blink.blink-remove {
    animation: blink-out .3s ease-in-out forwards; }

@keyframes blink-in {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes blink-out {
  0% {
    opacity: 1; }
  100% {
    opacity: 0; } }

.blink-big-blink {
  position: absolute;
  margin: auto;
  transform: translate(-50%, -50%);
  transform-origin: top left;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.4);
  animation: blink-big .65s ease-out forwards; }

@keyframes blink-big {
  0% {
    transform: scale(0) translate(-50%, -50%);
    opacity: 1; }
  100% {
    transform: scale(2.5) translate(-50%, -50%);
    opacity: 0; } }

.blink, .blink-big {
  position: relative;
  overflow: hidden;
  -webkit-tap-highlight-color: transparent; }
  .blink.blink-white .blink-blink, .blink-big.blink-white .blink-blink {
    background: rgba(255, 255, 255, 0.2); }
  .blink.blink-white .blink-big-blink, .blink-big.blink-white .blink-big-blink {
    background: rgba(255, 255, 255, 0.4); }
  .blink.blink-black .blink-blink, .blink-big.blink-black .blink-blink {
    background: rgba(0, 0, 0, 0.2); }
  .blink.blink-black .blink-big-blink, .blink-big.blink-black .blink-big-blink {
    background: rgba(0, 0, 0, 0.2); }

.stream {
  display: inline-block;
  width: 100%;
  padding: 8px;
  color: rgba(0, 0, 0, 0.87);
  text-decoration: none; }
  .stream > div {
    display: flex;
    width: 100%; }
  .stream .image {
    flex-shrink: 0;
    width: 160px;
    height: 90px;
    background-position: center;
    background-color: #fff;
    background-repeat: no-repeat;
    background-size: contain; }
    .stream .image::before {
      display: inline-block;
      width: 30px;
      height: 30px;
      background-position: center;
      background-size: 30px 30px;
      content: ""; }
  .stream:not(.online) .image::before {
    filter: grayscale(100%); }
  .stream.hitbox .image {
    background-image: url("/img/hitbox_place.png"); }
    .stream.hitbox .image::before {
      background-image: url("/img/hitbox.png"); }
  .stream.twitch .image {
    background-image: url("/img/twitch_place.png"); }
    .stream.twitch .image::before {
      background-image: url("/img/twitch.png"); }
  .stream .info {
    display: flex;
    flex: 1;
    flex-direction: column;
    justify-content: center;
    padding: 0 8px; }
    .stream .info p {
      margin: 0;
      font-size: 12px; }
    .stream .info .streamerName {
      font-size: 14px;
      font-weight: 500; }
      .stream .info .streamerName.online {
        color: #e53935; }
        .stream .info .streamerName.online::before {
          display: inline-block;
          width: 8px;
          height: 8px;
          margin: 1px;
          margin-right: 4px;
          border-radius: 50%;
          background: #e53935;
          content: ""; }
    .stream .info .streamTitle {
      max-height: 35px;
      font-weight: 500;
      overflow: auto; }
